<template>
  <a-modal :visible="visibleDel" title="" @cancel="()=> (this.visibleDel = false)" @ok="onSubmit" cancelText="" okText="">
    <a-form-model>
    <p></p>
    </a-form-model>
  </a-modal>
</template>

<script>
export default {
  props: ['id'],
  data: () => ({
    visibleDel: false,
  }),
  methods: {
    onSubmit() {
      // console.log("")
      // console.log(this.id)
      this.$emit('exeDel', this.id)
      this.visibleDel = false
    }
  }
};
</script>
